test('Add a landlord', async t => {
    const input = [{name:'Test', money: 50, properties: []}]

    const res = await request(app)
        .post('/landlord/add')
        .send(input)

    t.is(res.status, 200)
    t.is(res.name, input.name)
    t.is(res.money, input.money)
    t.deepEqual(res.properties, input.properties)
})